  		}
  		.billing-botton3{
  			width: 100%;
  			.da{
  				float: right;
  			}
  			
  		}
  	}
  }
  }
}
.showw{
  position: absolute;
  	width: 100%;
  	height: 2000px;
  	top: 0;
  	left: 0;
  	z-index: 20;
    background: rgba(0,0,0,0.4);
    .trajectory{
    width: 500px;
    height: 500px;
    background: #fff;
    border: 1px solid #e9e9e9;
    border-radius: 10px;
    position: absolute;
  	left: 50%;
  	top: 20%;
  	margin-left: -250px;
    .trajectory-title{
      height: 50px;
      border-bottom:1px solid #E9E9E9;
      line-height: 50px;
      h2{
        margin-left: 15px;
        float: left;
      }
      span{
        width: 50px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-weight: 700;
        float: right;
      }
    }
    .trajectory-content{
        .el-tabs{
          width: 440px;
          height: 420px;
          border: 0;
          margin: 20px auto 0;
          box-shadow: 0 0 0 0;
          .el-tabs__nav-scroll{
            border: 0;
            border-bottom: 2px solid #3051AE;
            background: #fff;
            .is-active{
              background: #3051AE;
              color: #fff;
            }
          }
          .el-tabs__content{
            border: 1px solid #e9e9e9;
            height: 300px;
            ul{
              li{
                height: 30px;
                line-height: 30px;
                border-bottom: 1px dashed #e9e9e9;
                span{
                  margin-left: 15px;
                }
                .date{
                  color: #c1c1c1;
                }
              }
            }
          }
        }
    }
  }
}
</style>



